.root {
  padding: 95px 30px;
}

.btn_group {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 20px;
  margin-top: 56px;
}

.time {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 24px;
}

.dots {
  display: inline-block;
  font-size: 28px;
  color: #fff;
}

.input {
  border: 2px solid #fff;
  padding: 13px 32px;
  border-radius: 5px;
  font-size: 28px;
  color: #fff;
  letter-spacing: 0.05rem;
  width: 138px;
  text-align: center;
  transition: 100ms linear;
  
  &::placeholder {
    font-size: 28px;
    letter-spacing: 0.05rem;
    color: #fff;
  }
  
  &:hover {
    background: rgba(120, 172, 252, 0.19);
  }
  
  &:focus {
    background: rgba(120, 172, 252, 0.35);
  }
}

.btn {
  font-weight: 400;
}

.input_time {
  border: 2px solid #fff;
  padding: 13px 32px;
  border-radius: 5px;
  font-size: 28px;
  color: #fff;
  letter-spacing: 0.05rem;
  width: 100%;
  transition: 100ms linear;
  margin-bottom: 40px;

  &::-webkit-calendar-picker-indicator {
    filter: invert(1);
  }

  &::placeholder {
    font-size: 28px;
    letter-spacing: 0.05rem;
    color: #fff;
  }
  
  &:hover {
    background: rgba(120, 172, 252, 0.19);
  }
  
  &:focus {
    background: rgba(120, 172, 252, 0.35);
  }
}

.timePlaceholoders {
  display: grid;
  margin-bottom: 10px;
  grid-template-columns: 1fr 30px 1fr 30px 1fr;
  span {
    display: inline-block;
    text-align: center;
    color: #fff;
  }
}

.validation {
  margin-top: 20px;
  color: rgb(248, 29, 29);
  font-size: 18px;
  font-weight: 600;
}