.root {
  letter-spacing: 0.05rem;
  transition: 100ms linear;
  color: #fff;
  border: 2px solid #fff;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 600;
  
  &:hover {
    background: rgba(120, 172, 252, 0.19);
  }
  
  &:active {
    background: rgba(120, 172, 252, 0.35);
  }
}

.lg {
  font-size: 1.75rem;
  border-radius: 8px;
  padding: 13px 32px;
}

.md {
  font-size: 0.875rem;
  border-radius: 5px;
  padding: 9px 20px;
}

.icon {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 13px;
  color: #fff;
}

.uppercase {
  text-transform: uppercase;
}

.staticWidth {
  padding-left: 0;
  padding-right: 0;
}

.active {
  background: #fff;
  box-shadow: inset 0px 2px 39px 4px rgba(58, 134, 204, 0.5);
  color: #2a7be4;
  icon {
    color: #2a7be4;
  }
  
  &:hover {
    background: #fff;
  }
  
  &:active {
    background: #fff;
  }
}