@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700;800;900&display=swap');

* {
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
  outline: none;
  box-sizing: border-box;
  background: none;
  border: none;
  user-select: none;
  font-family: 'Montserrat', sans-serif;
}

button, button * {
  cursor: pointer;
}

svg {
  pointer-events: none;
}

body, html {
  overflow-x: hidden;
}