.root {
  background: rgba(0, 0, 0, 0.45);
  backdrop-filter: blur(4px);
  position: fixed;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  z-index: 1000;
  opacity: 0;
}

.wrap {
  background: rgba(120, 172, 252, 0.35);
  backdrop-filter: blur(40px);
  border-radius: 18px;
  min-width: 200px;
  min-height: 200px;
  position: absolute;
  left: 50%;
  transform: translate(-50%, calc(-50% + 50vh));
  z-index: 2000;
}

.open {
  animation: fadeIn 200ms linear forwards;
}

.close {
  animation: fadeOut 200ms linear forwards;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}