.root {
  border-radius: 18px;
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(5px);
  padding: 16px 18px;
  color: #fff;
  * {
    color: #fff;
  }
}

.title {
  display: flex;
  align-items: center;
  justify-content: space-between;
  span {
    display: flex;
    align-items: center;
  }
  input {
    display: flex;
    align-items: center;
    font-size: 16px;
    max-width: 60px;
    text-align: right;
    height: 100%;
  }
}

.range {
  margin-top: 20px;
  -webkit-appearance: none; 
  width: 100%; 
  background: transparent; 
  &::-webkit-slider-thumb {
    -webkit-appearance: none;
    height: 28px;
    width: 28px;
    border-radius: 500px;
    margin-top: -7px;
    background: #ffffff;
    cursor: pointer;
    transition: 100ms linear;
    &:hover {
      background: #c7e4f1;
    }
    &:active {
      background: #97d7f5;
    }
  }
  &:focus {
    outline: none; 
  }
  &::-ms-track {
    width: 100%;
    cursor: pointer;
  
    background: transparent; 
    border-color: transparent;
    color: transparent;
  }
  &::-webkit-slider-runnable-track {
    width: 100%;
    height: 16px;
    cursor: pointer;
    background: none;
    border: 1px solid #fff;
    border-radius: 1.3px;
    border-radius: 100px;
  }
}

.input_wrap {
  display: inline-block;
}