.root {
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(5px);
  padding: 16px 18px;
  border-radius: 18px;
  display: flex;
  flex-direction: column;
  gap: 10px;
  transition: 100ms linear;
  
  &:hover {
    background: rgba(120, 172, 252, 0.19);
  }
  
  &:active {
    background: rgba(120, 172, 252, 0.35);
  }
}

.row {
  font-size: 24px;
  letter-spacing: 0.1rem;
  display: flex;
  gap: 3px;
  color: #fff;
}

.wrap {
  position: relative;
}

.refresh_btn {
  position: absolute;
  bottom: calc(100% + 25px);
  left: 0;
}