.root {
  display: block;
}

.menuBtn {
  position: fixed;
  top: 30px;
  left: 30px;
}

.sidebar {
  position: fixed;
  left: 0;
  top: 0;
  transform: translateX(-100%);
}

.sidebar--open {
  transform: translateX(0);
}

.footer {
  position: fixed;
  bottom: 0;
  left: 0;
}