.root {
  letter-spacing: 0.05rem;
  transition: 100ms linear;
  color: #fff;
  text-align: left;
  display: flex;
  align-items: center;
  justify-content: space-between;
  
  &:hover {
    background: rgba(120, 172, 252, 0.19);
  }
  
  &:active {
    background: rgba(120, 172, 252, 0.35);
  }
}

.icon_left, .icon_right {
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
}

.icon_left {
  margin-right: 5px;
}

.outlined {
  width: 100%;
  border: 2px solid #fff;
  padding: 14px;
  border-radius: 8px;
}

.outlined {
  width: 100%;
}

.active {
  background: #fff;
  color: #000;
  icon {
    color: #000;
  }
}

.text {
  border-radius: 20px;
  padding: 5px 10px;
}