.root {
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(5px);
  border-radius: 8px;
  border: 1px solid rgba(rgb(111, 188, 240), 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  width: 52px;
  height: 52px;
  color: #fff;
  transition: 100ms linear;
  
  &:hover {
    background: rgba(120, 172, 252, 0.19);
  }
  
  &:active {
    background: rgba(120, 172, 252, 0.35);
  }
}